<template>
  <div class="home_right_box article">
    <!--推荐模板标签-->
    <li class="reveal-bottom" v-for="(i,index) in baseData.articleData" :key="index">
      <i>
        <span :title="i.articleTitle">
          <img :src="i.src" :alt="i.articleTitle" @click.prevent="selectArticle(i.articleId)"/>
        </span>
      </i>
      <h3>
        <span v-if="false">[ 顶 ]</span>
        <span @click="selectArticle(i.articleId)">{{i.articleTitle}}</span>
      </h3>
      <p>{{i.subTitle}}</p>
    </li>
  </div>
</template>

<script>
import scrollReveal from "scrollreveal";
export default {
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal(".reveal-bottom", {
      // 动画的时长
      duration: 1000,
      // 延迟时间
      delay: 500,
      // 动画开始的位置，'bottom', 'left', 'top', 'right'
      origin: "bottom",
      // 回滚的时候是否再次触发动画
      reset: false,
      // 在移动端是否使用动画
      mobile: false,
      // 滚动的距离，单位可以用%，rem等
      distance: "20px",
      // 其他可用的动画效果
      opacity: 0.001,
      easing: "linear",
      scale: 0.9,
    });
  },
  methods: {
    selectArticle(articleId) {
      console.log(11)
      this.$router.push({
        name: "details", //这个name就是你刚刚配置在router里边的name
        params: {
          articleId: articleId,
        },
      });
    },
  },
};
</script>
<style lang="scss"  scoped>
.article{
  width: 100% !important;
}
</style>


